import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import About from '@/components/About'
import Others from '@/components/Others'
import Contact from '@/components/Contact'
import Project from '@/components/Project'
import Pan from '@/components/Pan'
import Ydt from '@/components/Ydt'
import Info from '@/components/Info'

Vue.use(Router);


export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/',
      component: Home
    },
    {
      path: '/',
      component: Home
    },
    {
      path: '/about',
      component: About
    },
    {
      path: '/others',
      component: Others
    },
    {
      path: '/contact',
      component: Contact
    },
    // {
    //   path: '/project',
    //   component: Project
    // },
    {
      path: '/project/pan',
      component: Pan
    },
    {
      path: '/project/ydt',
      component: Ydt
    },
    {
      path: '/info',
      component: Info
    },
  ]
})



