<template>
  <div class="home" >
    <div class="img"></div>
    <p class="content">Whatever you believe,you become.</p>
    <p class="text">Next</p>
    <router-link to="/about">
      <img src="../assets/circle.png" alt="" class="home_circle">
    </router-link>
  </div>
</template>
<script>
    export default {
        data() {
            return {}
        }
    }
</script>

<style scoped>
  .img{
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url("../assets/home.jpg");
    background-size:cover;
  }

  .content{
    position: absolute;
    color: white;
    width: 100%;
    text-align: center;
    top: 50%;
    margin-left: -2rem;
    font-size: 4em;
    line-height: 1;
  }

  .text{
    position: absolute;
    color: white;
    right: 10%;
    margin-right: 2em;
    bottom: 10%;
    font-size: 4em;
  }

  .home_circle {
    width: 6em;
    position: absolute;
    bottom: 10%;
    right: 10%;
  }
</style>



